import { useEffect, useState } from "react";
import Button from "../components/Button";
import Main from "../components/Main";
import { useDispatch, useSelector } from "react-redux";
import { login } from "../components/CitiesSlice";
import { useNavigate } from "react-router-dom";

const fakeUser = {
  email: "jack@gmail.com",
  password: "123456",
};

function Login() {
  const navigate = useNavigate();
  const [email, setEmail] = useState(fakeUser.email);
  const [password, setPassword] = useState(fakeUser.password);
  const dispatch = useDispatch();
  const { loggedin } = useSelector((state) => state.cities);
  useEffect(
    function () {
      if (loggedin) navigate("/app");
    },
    [loggedin, navigate]
  );
  function handleLogin(e) {
    e.preventDefault();
    if (email === fakeUser.email && password === fakeUser.password)
      dispatch(login());
  }
  return (
    <Main className=" pb-44">
      <form
        onSubmit={handleLogin}
        className="bg-[#42484D] flex flex-col py-5 px-8 rounded-md max-w-[500px] mx-auto gap-2 mt-16 "
      >
        <label className=" text-[18px] font-medium">Email address</label>
        <input
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          placeholder="email"
          className="bg-[#D6DEE0] h-10 rounded-md pl-4 mb-4 text-black"
        />
        <label className=" text-[18px] font-medium ">Password</label>
        <input
          value={password}
          onChange={(e) => setPassword(e.target.value)}
          placeholder="password"
          className="bg-[#D6DEE0] h-10 rounded-md pl-4 mb-4 text-black"
        />
        <Button className="w-fit py-2 px-4">LOGIN</Button>
      </form>
    </Main>
  );
}

export default Login;
